	import back from '@/components/back'

	export default {
		components: {
			back
		},
		data() {
			return {
				title: "推荐详情",
				isRecommend: false,
				recomMsg: null,
				info:null,
				totalPrice:null,
				showBtn:false,
				patient: this.$store.state.patient,
			}
		},
		created() {
			if(this.$store.state.recomMsg){
				this.recomMsg = this.$store.state.recomMsg
			}
			if(this.$route.query.fromIMForAPP){
				this.recomMsg = {
					DataSource: this.$route.query.DataSource,//数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
					DataId: this.$route.query.DataId,//数据来源Id
				}
			}
			/*this.recomMsg = {
				DataSource: 2,//数据来源(1：常用推荐，2：历史推荐，3：新增推荐)
				DataId: 11,//数据来源Id
				fromIM:true,
			}*/
			if(this.recomMsg) {
				this.pull()
			} else {
				this.$toast("暂未选择推荐的商品！")
			}
			console.log(this.$store.state.inspctArea)
		},
		mounted() {

		},
		beforeDestroy() {
			window.onscroll = null
		},
		methods: {
			showPopup() {
				//调用原生方法显示弹窗
				if(!this.info){
					return false;
				}
				if(this.recomMsg && this.recomMsg.fromIM && this.patient && this.patient.PatientId){
					//从IM里面过来
					let PatientIdList = []
					PatientIdList.push(this.patient.PatientId)
					this.$post('/PlatFormAPI/SpecialistHosGroup/SendGoodsRecommend', {
						DataSource: this.recomMsg.DataSource,
						DataId:this.recomMsg.DataId,
						PatientIdList:PatientIdList
					}).then(o => {
						//推荐成功，调回IM里面
						const data = '123'
                		this.$bridge.callhandler('BackToIM', data)
					})
				}else{
					this.recomMsg.LinkAddress = this.info.LinkAddress
		            this.$bridge.callhandler('SendGoodsRecommend', JSON.stringify(this.recomMsg))
				}
			},
			goEdit() {
				//编辑
				this.$store.dispatch('pushEditRecomDetails', this.info)
				this.$router.push("/prefRecommend/reDetailEdit")
			},
			changeRecom(){
				this.isRecommend = !this.isRecommend
				this.$post('/PlatFormAPI/SpecialistHosGroup/SetGoodsRecommendUsed', {
					DataSource: this.recomMsg.DataSource,
					DataId:this.recomMsg.DataId,
					IsOn:this.isRecommend?1:0
				}).then(o => {

				})
			},
			pull() {
				this.$post('/PlatFormAPI/SpecialistHosGroup/GetGoodsRecommendInfo', {
					DataSource: this.recomMsg.DataSource,
					DataId:this.recomMsg.DataId
				}).then(o => {
					if(o){
						this.showBtn = true
						let num = this.addTotal(o.OtherAmount,o.AllPrice)
						this.info = o
						this.totalPrice = this.subTotal(num,o.CouponAmount)
					}
				})
			},
			addTotal(num1,num2){
	        	//计算总价格 - 加法
	        	let a = Number(num1) // 运费、自取费用、代煎费
	            let b = Number(num2) //当前总计的价格
	            let c = 0
	            let d = 0
	            let e = 0
				try{
					c=a.toString().split(".")[1].length
				}catch(e){
					c=0
				}
				try{
					d=b.toString().split(".")[1].length
				}catch(e){
					d=0
				}
				e=Math.pow(10,Math.max(c,d))
				return ((a*e+b*e)/e).toFixed(2)
	       	},
	       subTotal(num1,num2){
	        	//计算总价格 - 减法
	        	let a = Number(num1) // 运费、自取费用、代煎费
	            let b = Number(num2) //当前总计的价格
	            let c = 0
	            let d = 0
	            let m = 0
	            //let n = 0
			    try{
			    	c=a.toString().split(".")[1].length
			    }catch(e){
			    	c=0
			    }
			    try{
			    	d=b.toString().split(".")[1].length
			    }catch(e){
			    	d=0
			    }
			    m=Math.pow(10,Math.max(c,d));
			    //动态控制精度长度
			    //n=(c>=d)?c:d;
			    return ((a*m-b*m)/m).toFixed(2)
	        }
		}
	}



